<template>
	<view class="exchange">
		<view class="head">
			<view @click="goback" class="arrow iconfont iconback"></view>
			<view class="h_title">我要兑换</view>
		</view>
		<view class="content">
			<view class="c_top">
				<view class="pic"><image :src="list.activeData.url" mode=""></image></view>
				<view class="c_top_text">
					<view class="t1">{{ list.activeData.title }}</view>
					<view class="t2">
						<text>{{ list.activeData.start_time }}</text>
						<button class="share" open-type="share">分享</button>
					</view>
				</view>
			</view>
			<view class="blank"></view>
			<view class="c_down">
				<view class="c_down_t">演出介绍</view>
				<view class="c_down_c"><u-parse :content="list.activeData.active_details" @preview="preview" @navigate="navigate"></u-parse></view>
			</view>
			<view class="btn" @click="exchange">立即兑换</view>
		</view>
	</view>
</template>

<script>
import uParse from '@/components/u-parse/u-parse.vue';
import { API } from '@/util/api.js';
export default {
	components: {
		uParse
	},
	data() {
		return {
			list: [],
			aid: '',
			adapte_active:''
		};
	},
	onLoad(option) {
		console.log(option)
		this.aid = option.id || option.aid;
		this.adapte_active=option.adapte_active
	},
	onShow() {
		var loginRes = this.checkLogin('/pages/ticket/exchange', '1', {aid:this.aid});

		if (loginRes) {
			this.getData();
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			console.log(res.target);
		}
		return {
			title:this.list.activeData.title,
			path: '/pages/singer/vocalConcert?id=' + this.aid
		};
	},
	methods: {
		getData() {
			this.request(API.exchangeDetail, 'get', {
				aid: this.aid
			}).then(res => {
				this.list = res.data;
			});
		},
		preview(src, e) {},
		navigate(href, e) {},
		goback() {
			uni.navigateBack({
				delta: 1
			});
		},
		exchange() {
			this.request(API.exchangeTick, 'get', {
				aid: this.aid,
				adapte_active:this.adapte_active
			}).then(res => {
				if (res.code === 10000) {
					uni.showToast({
						title: '兑换成功',
						duration: 2000
					});
					setTimeout(()=>{
						uni.navigateTo({
						    url: '/pages/ticket/ticketBalance'
						})
					},2000)
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none',
						duration: 2000
					});
				}
			});
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/ticket/exchange.scss';
</style>
